import React, { useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import PostMedia from "./postMedia";
import MultiImagePopup from "../popup/multiImagePopup";

const MultiPostMedia = (props) => {

  const [showRecentIndexData,setShowRecentIndexData] = useState()
  const [showRecentPostModal,setShowRecentPostModal] = useState()
  const [showRecentPostData,setShowRecentPostData] = useState()


  const recentPostToggle = (data, index) => event => {
    setShowRecentPostModal(true)
    setShowRecentPostData(data)
    setShowRecentIndexData(index)
  }

  const showRecentPostCallback = (dataEvent) => {
    setShowRecentPostModal(dataEvent)
    setShowRecentPostData(null)
    setShowRecentIndexData(null)
  }

  const settings = {
    type: "slide",
    rewind: false,
    speed: 1000,
    loop: true,
    interval: 7000,
    drag: true,
    perPage: 4,
    gap: 5,
    autoplay: false,
    padding: "0",
    pagination: false,
    arrows: true,
  };

  return (
    <>
      {props.postsData && Object.keys(props.postsData).length > 4 ? (
        <Splide className="recent-post m-0" hasTrack={false} options={settings}>
          <div  onClick={recentPostToggle([{ ...props.postsData }], 0)}> 
          <SplideTrack>
            {props.postsData.map((asset, index) => {
              return (
                <SplideSlide key={index}>
                  <PostMedia
                    multiPost={1}
                    postFile={asset.postFile}
                    postId={asset.postId}
                    className={"img-fluid"}
                  />
                </SplideSlide>
              );
            })}
          </SplideTrack>
         </div> 
        </Splide>
      ) : (
        props.postsData.map((asset, index) => {
          return (
           <div className="d-inline-block" onClick={recentPostToggle([{ ...props.postsData }], 0)}> 
            <PostMedia
              multiPost={1}
              postFile={asset.postFile}
              postId={asset.postId}
              className={"img-fluid"}
            />
           </div> 
          );
        })
      )}

     { showRecentPostData ? <MultiImagePopup showRecentPostCallback={showRecentPostCallback} modalToggle={setShowRecentPostModal} showRecentPostData={showRecentPostData} showRecentIndexData={showRecentIndexData} /> : null}
    </>
  );
};

export default React.memo(MultiPostMedia);
